/**** wui-file Start ****/
/*透明度兼容IE8 开始*/
.dropzone.dz-drag-hover .dz-message {
    filter: alpha(Opacity=50);
    opacity: 0.50;
    -moz-opacity: 0.50;
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=50);
    -MS-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
}

.dropzone .dz-preview:hover .dz-details {
    filter: alpha(Opacity=100);
    opacity: 1;
    -moz-opacity: 1;
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
    -MS-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}

.dropzone .dz-preview.dz-file-preview .dz-details {
    filter: alpha(Opacity=100);
    opacity: 1;
    -moz-opacity: 1;
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
    -MS-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}

.dropzone .dz-preview .dz-details {
    filter: alpha(Opacity=0);
    opacity: 0;
    -moz-opacity: 0;
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
    -MS-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
}

.dropzone .dz-preview.dz-error .dz-error-mark {
    filter: alpha(Opacity=100);
    opacity: 1;
    -moz-opacity: 1;
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
    -MS-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}

.dropzone .dz-preview .dz-success-mark,
.dropzone .dz-preview .dz-error-mark {
    filter: alpha(Opacity=0);
    opacity: 0;
    -moz-opacity: 0;
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
    -MS-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
}

.dropzone .dz-preview.dz-processing-success-show .dz-success-mark {
    filter: alpha(Opacity=100);
    opacity: 1;
    -moz-opacity: 1;
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
    -MS-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}

.dropzone .dz-preview.dz-processing .dz-progress {
    filter: alpha(Opacity=100);
    opacity: 1;
    -moz-opacity: 1;
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
    -MS-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}

.dropzone .dz-preview.dz-complete .dz-progress {
    filter: alpha(Opacity=0);
    opacity: 0;
    -moz-opacity: 0;
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
    -MS-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
}

.dropzone .dz-preview .dz-progress {
    filter: alpha(Opacity=100);
    opacity: 1;
    -moz-opacity: 1;
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
    -MS-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}

.dropzone .dz-preview.dz-error:hover .dz-error-message {
    filter: alpha(Opacity=100);
    opacity: 1;
    -moz-opacity: 1;
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
    -MS-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}

.dropzone .dz-preview .dz-error-message {
    filter: alpha(Opacity=0);
    opacity: 0;
    -moz-opacity: 0;
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
    -MS-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
}

/*透明度兼容IE8 结束*/

.dropzone {
    /*min-height: 50px*/
    min-height: 124px;
    padding: 10px 10px 0 10px;
    display: inline-block;
    border: 2px dashed #e5e5e5;
}

.dropzone .dz-message {
    margin: 0 0 10px 0;
    height: 100px;
    float: left;
}

.dropzone .file-add-icon {
    height: 100px;
    width: 100px;
    vertical-align: middle;
    text-align: center;
    /*background: url(../images/button_100x100.png);*/
    /*background: url(../images/picture.jpg);*/
}

.dropzone .dz-message .perview-a {
    display: block;
    width: 100%;
    height: 100%;
}

.dropzone .dz-message .perview-a img {
    border: none;
    width: 100%;
    height: 100%;
}

.dropzone .file-add-icon:hover {
    cursor: pointer;
}

.dropzone .dz-preview.dz-image-preview {
    border: 1px solid #efefef;
    /* box-shadow: 2px 2px 5px 0 #a2958a; */
    border-radius: 10px !important;
    margin: 0 10px 10px 0;
}

.dropzone .dz-preview.dz-image-preview img {
    width: 100%;
    text-align: center;
}

.dropzone .dz-preview .dz-image {
    border-radius: 10px !important;
}

.dropzone .dz-preview .dz-remove,
/*.dropzone .dz-preview .dz-upload-link,*/
.dropzone .dz-preview .dz-edit {
    display: none;
    border: none;
    background: transparent;
}

.dropzone .dz-preview .dz-remove {
    color: #fa144f;
}

.dropzone .dz-preview .dz-upload-link,
.dropzone .dz-preview .dz-download-link,
.dropzone .dz-preview .dz-edit {
    color: #F1A221;
    font-size: 16px;
    cursor: pointer;
    position: absolute;
    left: 3px;
    top: 3px;
    z-index: 40;
    height: 22px;
    width: 22px;
    border-radius: 16px !important;
    text-align: center;
    line-height: 16px;
}


.dropzone .dz-preview .dz-download-link{
    color: #4B83FA;
}

.dropzone .dz-preview .dz-remove i,
.dropzone .dz-preview .dz-upload-link i,
.dropzone .dz-preview .dz-download-link i,
.dropzone .dz-preview .dz-edit i {
    cursor: pointer;
}

/*.dropzone .dz-preview .dz-error-mark,
.dropzone .dz-preview .dz-success-mark{
    border-radius: 27px!important;
    background-color: #fff;
}*/
.dropzone .dz-preview .dz-error-mark i,
.dropzone .dz-preview .dz-success-mark i {
    width: 54px;
    height: 54px;
    text-align: center;
    vertical-align: middle;
    font-size: 60px;
    line-height: 54px;
    filter: alpha(Opacity=80);
    opacity: 0.80;
    -moz-opacity: 0.80;
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=80);
    -MS-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=80)";
}

.dropzone .dz-preview .dz-error-mark i {
    color: red;
}

.dropzone .dz-preview .dz-success-mark i {
    color: green;
}

.dropzone .dz-preview .img-btn-mask {
    border-radius: 10px !important;
}

.dropzone .dz-preview:hover .img-btn-mask,
.dropzone .dz-preview:hover .dz-remove,
.dropzone .dz-preview:hover .dz-upload-link,
.dropzone .dz-preview:hover .dz-edit {
    display: block;
}

.dropzone .dz-preview.dz-success .dz-upload-link,
.dropzone .dz-preview.dz-error .dz-upload-link,
.dropzone .dz-preview.dz-complete .dz-upload-link {
    display: none;
}

.dropzone .dz-preview.dz-success:hover .dz-download-link,
/* .dropzone .dz-preview.dz-error .dz-download-link, */
.dropzone .dz-preview.dz-complete:hover .dz-download-link {
    display: block;
}

.dropzone .btn-upload-ie {
    display: inline-block;
    margin-top: 20px;
    margin-left: 10px;
}

.dropzone .dz-preview .dz-details {
    /*font-size: 13px;*/
    padding: 2em 0.3em;
}

.dropzone.wui-file-icon .dz-message {
    display: inline-block;
    position: static;
    width: auto;
}

.dropzone.wui-file-icon.dz-max-files-reached .dz-message {
    display: none;
}

.dropzone.wui-file-icon.dz-max-files-reached object {
    /*display: none;
    width: 0px !important;
    height: 0px !important;*/

    opacity:0;
    width: 1px !important;
    height: 1px !important;
}

.prev-icon.fa {
    /*display:block!important;*/
    position: absolute;
    top: 0px;
    display: block;
    height: 100%;
    line-height: 100% !important;
    width: 100%;
    margin: 0 auto;
    padding-left: 20px;
    padding-top: 15px;
    color: #337ab7
}

.dropzone .dz-preview {
    min-height: 50px;
    margin: 0 10px 10px 0;
    float: left;
}

.dropzone .dz-preview .dz-image img {
    width: 100%;
}

.dropzone .dz-preview:hover .dz-size,
.dropzone .dz-preview:hover .dz-filename {
    display: block;
}

.dropzone .dz-preview .dz-size,
.dropzone .dz-preview .dz-filename {
    display: none;
}

.dropzone .dz-preview .dz-progress {
    top: auto;
    bottom: 10px;
}

.table .control-content .dropzone {
    width: 144px;
}
/**** wui-file End ****/