
/* page-sidebar */
.page-sidebar-closed.page-sidebar-closed-hide-logo .page-header.navbar .page-logo {
	width: 50px;
	overflow: hidden
}

.sidebar-toggler {
	width: 11px;
	height: 50px;
	background: #DEDEDE url(../images/copus-l.png) no-repeat center center;
	cursor: pointer;
	/*position: absolute;*/
	position: fixed;
	margin-top: -25px;
	/*left: 220px;*/
	left: 200px;
	top: 50%;
	z-index: 999;
}

.page-sidebar-closed .sidebar-toggler {
	background: #DEDEDE url(../images/copus-r.png) no-repeat center center;
	left: 50px;
}

.page-sidebar-closed.page-sidebar-closed-hide-logo .page-header.navbar .page-logo img {
	margin-left: -35px;
}

.page-sidebar.navbar-collapse,
.page-sidebar-closed.page-sidebar-fixed .page-sidebar:hover.navbar-collapse {
	padding: 0;
	box-shadow: none;
}

.page-sidebar .page-sidebar-menu,
.page-sidebar-closed.page-sidebar-fixed .page-sidebar:hover .page-sidebar-menu {
	list-style: none;
	margin: 0;
	padding: 0;
}

.page-sidebar .page-sidebar-menu>li,
.page-sidebar-closed.page-sidebar-fixed .page-sidebar:hover .page-sidebar-menu>li {
	display: block;
	margin: 0;
	padding: 0;
	border: 0px;
}


.page-sidebar .page-sidebar-menu>li.start>a,
.page-sidebar-closed.page-sidebar-fixed .page-sidebar:hover .page-sidebar-menu>li.start>a {
	border-top-color: transparent !important;
}

.page-sidebar .page-sidebar-menu>li.last>a,
.page-sidebar-closed.page-sidebar-fixed .page-sidebar:hover .page-sidebar-menu>li.last>a {
	border-bottom-color: transparent !important;
}

.page-sidebar .page-sidebar-menu>li>a,
.page-sidebar-closed.page-sidebar-fixed .page-sidebar:hover .page-sidebar-menu>li>a {
	display: block;
	position: relative;
	margin: 0;
	border: 0px;
	padding: 10px 15px;
	text-decoration: none;
	font-size: 14px;
	font-weight: 300;
}

.page-sidebar .page-sidebar-menu>li a.nav-link:hover,
.page-sidebar-closed.page-sidebar-fixed .page-sidebar:hover .page-sidebar-menu>li a.nav-link:hover {
	cursor: pointer;
}

.page-sidebar .page-sidebar-menu>li>a>i,
.page-sidebar-closed.page-sidebar-fixed .page-sidebar:hover .page-sidebar-menu>li>a>i {
	font-size: 16px;
	margin-right: 5px;
	text-shadow: none;
}

.page-sidebar .page-sidebar-menu>li>a>i.glyphicon,
.page-sidebar-closed.page-sidebar-fixed .page-sidebar:hover .page-sidebar-menu>li>a>i.glyphicon {
	margin-left: 1px;
	margin-right: 4px;
}

.page-sidebar .page-sidebar-menu>li>a>[class^="icon-"],
.page-sidebar-closed.page-sidebar-fixed .page-sidebar:hover .page-sidebar-menu>li>a>[class^="icon-"] {
	margin-left: 1px;
	margin-right: 4px;
}

.page-sidebar-fixed .page-sidebar .page-sidebar-menu>li>a,
.page-sidebar-fixed .page-sidebar-closed.page-sidebar-fixed .page-sidebar:hover .page-sidebar-menu>li>a {
	transition: all 0.2s ease;
}

.page-sidebar-reversed.page-sidebar-fixed .page-sidebar .page-sidebar-menu>li>a,
.page-sidebar-reversed.page-sidebar-fixed .page-sidebar-closed.page-sidebar-fixed .page-sidebar:hover .page-sidebar-menu>li>a {
	transition: none;
}

.page-sidebar .page-sidebar-menu>li.heading,
.page-sidebar-closed.page-sidebar-fixed .page-sidebar:hover .page-sidebar-menu>li.heading {
	padding: 15px 15px 15px 15px;
}

