@charset "UTF-8";

body {
	font-family: "Microsoft Yahei", "Helvetica Neue", "Helvetica, Arial", "sans-serif";
	background: #fff;
}

body.page-index-body{
	min-width: 1280px;
}

body.full-screen{
	overflow: hidden;
}

ul {
	margin: 0;
	padding: 0;
}

/* page-header */

.page-header.navbar {
	width: 100%;
	padding: 0 20px 0 20px;
	margin: 0;
	border: 0px;
	padding: 0px;
	box-shadow: none;
	height: 60px;
	min-height: 60px;
	filter: none;
	background-image: none;
}

.page-header.navbar.navbar-fixed-top,.page-header.navbar.navbar-static-top {
	z-index: 9995;
	min-width: 1280px;
}

.page-header.navbar .page-logo {
	float: left;
	/*width: 220px;*/
	width: 200px;
	display: block;
	height: 60px;
	line-height: 0px;
	font-size: 24px;
	text-align: center;
}

.page-header.navbar .page-logo>a {
	display: inline-block;
	height: 100%;
	font-size: 20px;
	color: #fff;
}

.page-title {
	font-size: 22px;
	color: #333;
	margin: 0 20px;
	line-height: 60px;
	font-weight: bold;
}

.page-header.navbar .top-menu {
	margin: 0;
	padding: 0;
	float: right;
}

.page-header.navbar .top-menu .navbar-nav {
	padding: 0;
	margin-right: 20px;
	display: block;
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown-user, 
.page-header.navbar .top-menu .navbar-nav>li.dropdown-nav{
	margin: 0px;
	padding: 0px 4px;
	height: 60px;
	display: inline-block;
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown>a{
	display: inline-block;
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown>.dropdown-toggle {
	margin: 0px;
	padding: 0;
	line-height:30px;
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown-nav>.dropdown-toggle{
	padding:15px 10px 15px 25px;
	padding-right: 0;
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown>.dropdown-toggle>i {
	font-size: 17px;
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown>.dropdown-toggle:focus {
	background: none;
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown .dropdown-menu {
	margin-top: 3px;
	-webkit-border-radius: 4px;
	-moz-border-radius: 4px;
	-ms-border-radius: 4px;
	-o-border-radius: 4px;
	border-radius: 4px;
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown .dropdown-menu:before {
	position: absolute;
	top: -7px;
	right: 9px;
	display: inline-block !important;
	border-right: 7px solid transparent;
	border-bottom: 7px solid #eee;
	border-left: 7px solid transparent;
	border-bottom-color: rgba(0, 0, 0, 0.2);
	content: '';
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown .dropdown-menu:after {
	position: absolute;
	top: -6px;
	right: 10px;
	display: inline-block !important;
	border-right: 6px solid transparent;
	border-bottom: 6px solid #fff;
	border-left: 6px solid transparent;
	content: '';
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown .dropdown-menu>li>a {
	color: #555;
	padding: 8px 20px;
}

.page-header .dropdown-nav .dropdown-toggle {
	border-left: 1px solid #e5e5e5;
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown-tool, 
.page-header.navbar .top-menu .navbar-nav>li.dropdown-user{
	padding: 15px 6px 15px 8px;
	height: 60px;
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown-user .dropdown-toggle>.username {
	display: inline-block;
	font-size: 13px;
	font-weight: 300;
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown-user .dropdown-toggle>img {
	margin-right: 5px;
	height: 30px;
	display: inline-block;
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown-user .dropdown-toggle>i {
	display: inline-block;
	margin: 0;
	font-size: 13px;
}

.page-header.navbar .top-menu .navbar-nav>li .dropdown-menu {
	right: 0;
	left: auto;
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown-user .dropdown-menu {
	width: 175px;
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown-user .dropdown-menu>li>a {
	font-size: 14px;
	font-weight: 300;
}

.page-header.navbar .top-menu .navbar-nav>li .dropdown-menu>li>a i {
	width: 15px;
	display: inline-block;
	margin-right: 9px;
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown-user .dropdown-menu>li>a .badge {
	margin-right: 10px;
}

.page-header.navbar .top-menu .navbar-nav>li.dropdown-tool .dropdown-menu>li>a {
	font-size: 14px;
	font-weight: 300;
}

.searchbar {
	margin-top: 14px;
	border: 1px solid #b3b3b3;
	height: 30px;
	width: 200px;
	overflow: hidden;
	margin-right:10px;
}

.searchbar input {
	width: 168px;
	height: 28px;
	line-height: 28px;
	vertical-align: top;
	border: 0;
	padding: 0 10px;
}

.searchbar span {
	display: inline-block;
	width: 25px;
	text-align: center;
	line-height: 30px;
	color: #b3b3b3;
	vertical-align: top;
	cursor: pointer;
	font-size: 18px;
}

/* page-container */
.page-container {
	margin: 0px;
	padding: 0px;
	position: relative;
}

.page-container:before,
.page-container:after {
	content: " ";
	display: table;
}

.page-container:after {
	clear: both;
}

.page-header-fixed .page-container {
	margin-top: 60px;
}


/* page-content */
.page-wrapper {
	padding: 15px 30px;
}

.page-content {
	margin-top: 0px;
	background-color: #fff;
}

.page-container-bg-solid .page-content {
	background: #eef1f5;
}

.page-header.navbar .page-logo-min {
	display: none;
}

.page-full-width .page-content {
	margin-left: 0px !important;
}

.page-full-width .page-header.navbar .page-logo {
	display: none !important;
}

.page-full-width .page-header.navbar .page-logo-min {
	display: block;
}

