/* page-footer */

.page-foot {
    position: fixed;
    bottom: 0px;
    left: 0px;
    right: 0;
    margin: 0px;
    box-sizing: border-box;
    text-align: right;
    z-index: 50;
    background: #f3f5f7;
}

.page-footer {
	padding: 8px 20px 5px 20px;
	font-size: 13px;
	height: 33px;
}

.page-footer:before,
.page-footer:after {
	content: " ";
	display: table;
}

.page-footer:after {
	clear: both;
}

.page-footer .page-footer-inner {
	display: inline-block;
}

.page-footer-fixed.page-footer-fixed-mobile .page-footer {
	position: fixed;
	left: 0;
	right: 0;
	z-index: 10000;
	bottom: 0;
}

.page-footer-fixed.page-footer-fixed-mobile.page-sidebar-fixed .page-footer {
	margin-left: 0 !important;
}

