.navbar-fixed-bottom, .navbar-fixed-top {
    position: absolute;
}

/***  
Page Loading       
***/
.page-on-load {
	background: #fefefe;
}

.page-on-load .page-header,
.page-on-load .page-container,
.page-on-load .page-footer,
.page-on-load>.clearfix {
	display: none;
	transition: all 2s;
}

.root-nav>a {
	padding: 0 10px !important;
	border-radius: 4px !important;
}

.fa-item {
	margin-bottom: 5px;
}

