
/* 表单的table布局 */
.table-layout {
	width: 100%;
}

.table-layout td {
	height: 50px;
}

.table-layout th {
	height: 50px;
	/*font-size:17px;*/
	/*letter-spacing: 7px;*/
}

.table-layout .control-title {
	width: 20%;
	text-align: right;
	padding: 5px 10px;
	font-size: 14px;
	color: #333;
}

.table-layout .control-content {
	padding: 5px 10px;
}

.wui-textarea {
	border: 1px solid #e5e5e5;
	padding: 10px;
	height: 120px;
	margin: 0;
	width: 100%;
	outline: none;
}

.wui-input,
.wui-select {
	width: 100%;
	height: 30px;
	padding: 3px 12px;
	border: 1px solid #e5e5e5;
}

.aid-text {
	color: #999;
	font-size: 12px;
	margin-top: 5px;
	margin-bottom: 0;
}

.wui-form-radio {
	display: inline-block;
	position: relative;
	margin-right: 20px;
}

.wui-form-radio input {
	position: relative;
	opacity: 0;
	vertical-align: top;
	width: 20px;
	height: 20px;
	z-index: 10;
}

.wui-form-radio label {
	position: absolute;
	top: 0;
	left: 0;
	display: inline-block;
	width: 20px;
	height: 20px;
	border-radius: 10px;
	border: 1px solid #ccc;
	border: 0\9;
	vertical-align: middle;
	margin-right: 5px;
	margin-bottom: 0;
}

p{
	margin: 0;
}

:root .wui-form-radio label {
	border: 1px solid #ccc;
}

:root .wui-form-check label {
	border: 1px solid #ccc;
}

.wui-form-radio input[type="radio"]:checked+label i {
	display: block;
	width: 10px;
	height: 10px;
	border-radius: 5px;
	margin: 4px auto;
}

.wui-form-check {
	display: inline-block;
	position: relative;
	margin-right: 20px;
}

.wui-form-check input {
	position: relative;
	z-index: 10;
	opacity: 0;
	vertical-align: top;
	width: 20px;
	height: 20px;
}

.wui-form-check label {
	display: inline-block;
	position: absolute;
	top: 0;
	left: 0;
	width: 20px;
	height: 20px;
	line-height: 20px;
	border-radius: 3px;
	border: 1px solid #ccc;
	border: 0\9;
	vertical-align: middle;
	margin-right: 5px;
	font-family: FontAwesome;
	font-size: 14px;
	text-align: center;
}

.wui-form-check input[type="checkbox"]:checked+label {
	padding-left: 0px;
}

.wui-form-check input[type="checkbox"]:checked+label::before {
	content: "\f00c";
}

/*单/复选块显示*/
.wui-block .wui-form-check,
.wui-block .wui-form-radio {
	display: block;
}

.input-group-addon{
    border-radius: 0px;
}
.wui-bg-default .input-group-addon,
.wui-bg-primary .input-group-addon,
.wui-bg-info .input-group-addon,
.wui-bg-danger .input-group-addon,
.wui-bg-warning .input-group-addon,
.wui-bg-success .input-group-addon{
    background: inherit;
    border: none;
}

.input-group-addon .wui-form-check input,
.input-group-addon .wui-form-check label,
.input-group-addon .wui-form-radio input,
.input-group-addon .wui-form-radio label{
    width: 14px;
    height: 14px;
    line-height: 14px;
}

.input-group-addon .wui-form-radio input[type=radio]:checked+label i{
	margin: 1px auto;
}

/* 表单验证 */
.form-body table {
	width: 100%;
}

.form-body table td {
	height: 40px;
	padding: 5px 10px;
}

.form-body table td.form-label {
	text-align: right;
	color: #333;
}

.wui-form-label,
table td.wui-form-label,
.table>tbody>tr>td.wui-form-label{
	text-align: right;
	color: #333;
	padding-top: 12px;
}

/*form table*/
/*.form-td-label {
    min-width: 50px;
    padding-right: 16px;
    text-align: right;
}*/
table>tbody>tr>td.form-td-label {
    min-width: 50px;
    padding-top: 12px;
    padding-right: 8px;
    text-align: right;
}

.form-td-content {
    max-width: 270px;
}

/* .form-td-content input,
.form-td-content select,
.form-td-content .wui-selectBox {
    width: 250px;
} */

.form-btn {
	display: inline-block;
	width: 25px;
}

.wui-table-fixed {
	table-layout: fixed;
	word-break: break-all;
}

.form-actions {
    padding: 10px 20px;
}

