
.fancytree-node.disable .fancytree-title {
    color: #d0cdcd;
}

.fancytree-selected span.fancytree-title {
    color: #000;
    font-style: normal;
}

tr.fancytree-disabled span.fancytree-title,
span.fancytree-node.fancytree-disabled span.fancytree-title {
    color: silver;
    font-weight: lighter;
}

