span.fancytree-checkbox {
    margin-left: 3px;
    background-position: -6px -47px;
}

span.fancytree-checkbox:hover {
    background-position: -30px -47px;
}

.fancytree-partsel span.fancytree-checkbox {
    background-position: -102px -47px;
}

.fancytree-partsel span.fancytree-checkbox:hover {
    background-position: -126px -47px;
}

.fancytree-selected span.fancytree-checkbox {
    background-position: -54px -47px;
}

.fancytree-selected span.fancytree-checkbox:hover {
    background-position: -78px -47px;
}

.fancytree-unselectable span.fancytree-checkbox {
    opacity: 0.4;
    filter: alpha(opacity=40);
}

.fancytree-unselectable span.fancytree-checkbox:hover {
    background-position: -6px -47px;
}

.fancytree-unselectable.fancytree-partsel span.fancytree-checkbox:hover {
    background-position: -126px -47px;
}

.fancytree-unselectable.fancytree-selected span.fancytree-checkbox:hover {
    background-position: -54px -47px;
}

.fancytree-disabled span.fancytree-checkbox,
.fancytree-disabled span.fancytree-checkbox:hover {
    opacity: 0.4;
    filter: alpha(opacity=40);
    /*background-position: -6px -47px;*/
}

.fancytree-ico-cf span.fancytree-icon:hover {
    background-position: 0px -24px;
}

/*------------------------------------------------------------------------------
 * Radiobutton icon
 * This is a customization, that may be activated by overriding the 'checkbox'
 * class name as 'fancytree-radio' in the tree options.
 *----------------------------------------------------------------------------*/
.fancytree-radio span.fancytree-checkbox {
    background-position: -6px -72px;
}

.fancytree-radio span.fancytree-checkbox:hover {
    background-position: -30px -72px;
}

.fancytree-radio .fancytree-partsel span.fancytree-checkbox {
    background-position: -102px -72px;
}

.fancytree-radio .fancytree-partsel span.fancytree-checkbox:hover {
    background-position: -126px -72px;
}

.fancytree-radio .fancytree-selected span.fancytree-checkbox {
    background-position: -54px -72px;
}

.fancytree-radio .fancytree-selected span.fancytree-checkbox:hover {
    background-position: -78px -72px;
}

.fancytree-radio .fancytree-unselectable span.fancytree-checkbox,
.fancytree-radio .fancytree-unselectable span.fancytree-checkbox:hover {
    background-position: -6px -72px;
}

.fancytree-radio .fancytree-disabled span.fancytree-checkbox,
.fancytree-radio .fancytree-disabled span.fancytree-checkbox:hover {
    opacity: 0.4;
    filter: alpha(opacity=40);
    background-position: -6px -72px;
}

.fancytree-ext-childcounter span.fancytree-childcounter,
.fancytree-ext-filter span.fancytree-childcounter {
    color: #fff;
    background: #777;
    border: 1px solid gray;
    position: absolute;
    top: -6px;
    right: -6px;
    min-width: 18px;
    height: 18px;
    line-height: 1;
    vertical-align: baseline;
    text-align: center;
    border-radius: 18px;
    padding: 2px;
    font-size: 9px;
}


/* 树控件样式修复 */
ul.fancytree-container {
    border: none;
    outline: none;
    padding: 5px 10px;
}

.context-menu-list {
    z-index: 100 !important;
}

span.fancytree-title:hover {
    background: #a6d0ec;
}

.ui-fancytree {
    height: 100%;
}

.wui-tree .input-group{
    margin-left: 10px;
    margin-right: 10px;
    /*margin-top: 10px;*/
    margin-bottom: 10px;
}

.wui-tree .input-group + div{
    margin-left: 10px; 
    margin-right: 10px;
}


