/*
*   form-table 
*/
.form-table .table {
    border: 1px solid #e5e5e5;
}

.form-table th,
.form-table .control-title {
    text-align: right;
    color: #666666;
    font-size: 14px;
}

.form-table .col4-fluid th,
.form-table .col4-fluid .control-title {
    width: 15%;
}

.table-notice {
    width: 100%;
    border: 1px solid #e5e5e5;
}

.table-notice tr th {
    height: 45px;
    background: #f2f3f8;
    border-top: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    text-align: left;
    padding: 0 20px;
}

.table-notice tr td {
    height: 45px;
    border-top: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    /*text-align: center;*/
    padding: 0 20px;
}

.table-notice tr .text-left {
    text-align: left;
}

.table-notice tr th {
    color: #8b9297;
}

.table-notice tr td {
    color: #666;
}

.table-notice tr td span {
    color: #c1c6ca;
}

.table-notice tr td i.fa {
    color: #c1c6ca;
}

.table-notice tr td em {
    font-style: normal
}

.table-notice tr td a {
    color: #4b83fa;
    /* text-decoration: underline; */
}

.table .control-title {
    text-align: right;
}

.table .control-content textarea {
    width: 100%;
    height: 100px;
}

.table .form-table-btns {
    text-align: center;
}
