
/** 自定义标签样式 **/
.wui-tagsinput {
    width: 100%;
    height: auto;
    max-height: 100%;
}

.wui-tagsinput .input-group-btn .btn {
    border: 1px solid #e5e5e5;
    padding: 4px 14px;
    float: left;
}

.tag-container {
    display: inline-block;
    background-color: rgb(255, 255, 255);
    vertical-align: top;
    /*width: 320px!important;*/
    width: 100%;
    min-height: 30px;
    /*line-height: 24px;*/
    border-width: 1px;
    border-style: solid;
    border-color: #e5e5e5;
    border-image: initial;
    padding: 2px 0px 2px 6px;
    height: auto;
    outline: 0;
    -webkit-appearance: none;
    text-decoration: none !important;
}

.tag-container.dropdown-toggle {
    cursor: pointer;
    border-radius: 0;
}

.tag-container input {
    vertical-align: top;
    border: none;
    outline: none;
    /*height: 100%;*/
    height: 24px;
    max-width: 100%;
    /*min-width: 80px;*/
    /*max-width: 80px;*/
}

.tag-container input.noFreeInput {
    width: 0px;
    cursor: pointer;
}

.tag-container .tag {
    margin-top: 2px;
    margin-right: 2px;
    display: inline-block;
    max-width: 100%;
    color: rgb(255, 255, 255);
    vertical-align: top;
    border-radius: 0em;
}

.tag-container .tag.wui-bg-info{
    color: #000;
    border: 1px solid #e5e5e5;
}

