.wui-tagsinput.input-group {
    border-collapse: inherit;
}

.tag-container .tag [data-role=remove]:after {
    cursor: pointer;
    font-family: arial;
    content: "x";
    padding: 0 2px 0 6px;
}

.chosen-append .tag-container {
    border: none;
    /*border-top: 1px solid #e5e5e5;*/
}

