.ui-tags {
    width: 100%;
}

.btn-group>.ui-tags-dropdown,
.dropdown-toggle>.ui-tags-dropdown,
.dropdown>.ui-tags-dropdown {
    margin-top: -5px;
}

.ui-tags .ui-tags-dropdown,
.ui-tags .ui-tags-dropdown>.dropdown-menu,
.ui-tags .ui-tags-dropdown li {
    background: #fff;
    width: 100%;
}

mark.highlight {
    background: #fff;
    color: red;
}

.ui-tags-dropdown .no-results {
    display: list-item;
    padding: 3px;
    background: #f5f5f5;
    margin: 0 5px;
    white-space: nowrap;
}

.ui-tags>.dropdown-menu:before,
.ui-tags>.dropdown-menu:after {
    border: none !important;
}

.ui-tags .ui-tags-dropdown li:not([ui-disabled="true"]):hover a {
    outline: 0;
    background-color: #428bca;
    color: #fff;
}

.ui-tags .ui-tags-dropdown.dropdown-menu li.divider {
    margin: 0px;
}

.ui-tags .ui-tags-dropdown.dropdown-menu li a span.check-mark {
    display: none;
}

.ui-tags .ui-tags-dropdown.dropdown-menu li[ui-selected="true"] a span.check-mark {
    position: absolute;
    display: inline-block;
    right: 15px;
    margin-top: 5px;
}

.ui-tags .ui-tags-dropdown.dropdown-menu li.dropdown-group a span.check-mark {
    position: absolute;
    display: inline-block;
    right: 15px;
    margin-top: 5px;
}

.ui-tags .ui-tags-dropdown.dropdown-menu li:hover a span.check-mark {
    color: #fff;
}

.ui-tags .ui-tags-dropdown.dropdown-menu li a.opt {
    position: relative;
    padding-left: 2.25em;
}

.no-item{
    color: #aaa;
}

