/*选择框*/
.wui-select-box {
    width: 220px \0;
    /*IE9 and below*/
}

.wui-select-box>.dropdown-toggle {
    width: 100%;
    padding-right: 25px;
    z-index: 1;
}

.wui-select-box>select {
    position: absolute !important;
    bottom: 0;
    left: 50%;
    width: 0.11px !important;
    height: 100% !important;
    padding: 0 !important;
    opacity: 0 !important;
    border: none;
}

.wui-select-box>select.mobile-device {
    top: 0;
    left: 0;
    display: block !important;
    width: 100% !important;
    z-index: 2;
}

.has-error .wui-select-box .dropdown-toggle,
.error .wui-select-box .dropdown-toggle {
    border-color: #b94a48;
}

.wui-select-box.fit-width {
    width: auto !important;
}

.wui-select-box:not([class*="col-"]):not([class*="form-control"]):not(.input-group-btn) {
    width: 220px;
}

.wui-select-box .dropdown-toggle:focus {
    outline: thin dotted #333333 !important;
    outline: 5px auto -webkit-focus-ring-color !important;
    outline-offset: -2px;
}
/*.wui-select-box .dropdown-toggle:hover {
    outline: thin dotted #333333 !important;
    outline: 5px auto -webkit-focus-ring-color !important;
    outline-offset: -2px;
}*/

.wui-select-box.form-control {
    margin-bottom: 0;
    padding: 0;
    border: none;
}

.wui-select-box.form-control:not([class*="col-"]) {
    width: 100%;
}

.wui-select-box.form-control.input-group-btn {
    z-index: auto;
}

.wui-select-box.btn-group:not(.input-group-btn),
.wui-select-box.btn-group[class*="col-"] {
    float: none;
    display: inline-block;
    margin-left: 0;
}

.wui-select-box.btn-group.dropdown-menu-right,
.wui-select-box.btn-group[class*="col-"].dropdown-menu-right,
.row .wui-select-box.btn-group[class*="col-"].dropdown-menu-right {
    float: right;
}

.form-inline .wui-select-box.btn-group,
.form-horizontal .wui-select-box.btn-group,
.form-group .wui-select-box.btn-group {
    margin-bottom: 0;
}

.form-group-lg .wui-select-box.btn-group.form-control,
.form-group-sm .wui-select-box.btn-group.form-control {
    padding: 0;
}

.form-inline .wui-select-box.btn-group .form-control {
    width: 100%;
}

.wui-select-box.btn-group.disabled,
.wui-select-box.btn-group>.disabled {
    cursor: not-allowed;
}

.wui-select-box.btn-group.disabled:focus,
.wui-select-box.btn-group>.disabled:focus {
    outline: none !important;
}

.wui-select-box.btn-group.bs-container {
    position: absolute;
}

.wui-select-box.btn-group.bs-container .dropdown-menu {
    z-index: 1060;
}

.wui-select-box.btn-group .dropdown-toggle .filter-option {
    display: inline-block;
    overflow: hidden;
    width: 100%;
    text-align: left;
    min-height: 28px;
}

.wui-select-box.btn-group .dropdown-toggle .caret {
    position: absolute;
    top: 50%;
    right: 12px;
    margin-top: -2px;
    vertical-align: middle;
}

.wui-select-box.btn-group[class*="col-"] .dropdown-toggle {
    width: 100%;
}

.wui-select-box.btn-group .dropdown-menu {
    min-width: 100%;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.wui-select-box.btn-group .dropdown-menu.inner {
    position: static;
    float: none;
    border: 0;
    padding: 0;
    margin: 0;
    border-radius: 0;
    -webkit-box-shadow: none;
    box-shadow: none;
}

.wui-select-box.btn-group .dropdown-menu li {
    position: relative;
}

.wui-select-box.btn-group .dropdown-menu li.active small {
    color: #fff;
}

.wui-select-box.btn-group .dropdown-menu li.disabled a {
    cursor: not-allowed;
}

.wui-select-box.btn-group .dropdown-menu li a {
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.wui-select-box.btn-group .dropdown-menu li a.opt {
    position: relative;
    padding-left: 2.25em;
}

.wui-select-box.btn-group .dropdown-menu li span.check-mark {
    display: none;
}

.wui-select-box.btn-group .dropdown-menu li a span.text {
    display: inline-block;
}

.wui-select-box.btn-group .dropdown-menu li small {
    padding-left: 0.5em;
}

.wui-select-box.btn-group .dropdown-menu .notify {
    position: absolute;
    bottom: 5px;
    width: 96%;
    margin: 0 2%;
    min-height: 26px;
    padding: 3px 5px;
    background: #f5f5f5;
    border: 1px solid #e3e3e3;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05);
    pointer-events: none;
    opacity: 0.9;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.wui-select-box.btn-group .no-results {
    padding: 3px;
    background: #f5f5f5;
    margin: 0 5px;
    white-space: nowrap;
}

.wui-select-box.btn-group.fit-width .dropdown-toggle .filter-option {
    position: static;
}

.wui-select-box.btn-group.fit-width .dropdown-toggle .caret {
    position: static;
    top: auto;
    margin-top: -1px;
}

.wui-select-box.btn-group.show-tick .dropdown-menu li.selected span.check-mark {
    position: absolute;
    display: inline-block;
    right: 15px;
    margin-top: 5px;
}

.wui-select-box.btn-group.show-tick .dropdown-menu li a span.text {
    margin-right: 34px;
}

.wui-select-box.show-menu-arrow.open>.dropdown-toggle {
    z-index: 1061;
}

.wui-select-box.show-menu-arrow .dropdown-toggle:before {
    content: '';
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-bottom: 7px solid rgba(204, 204, 204, 0.2);
    position: absolute;
    bottom: -4px;
    left: 9px;
    display: none;
}

.wui-select-box.show-menu-arrow .dropdown-toggle:after {
    content: '';
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-bottom: 6px solid white;
    position: absolute;
    bottom: -4px;
    left: 10px;
    display: none;
}

.wui-select-box.show-menu-arrow.dropup .dropdown-toggle:before {
    bottom: auto;
    top: -3px;
    border-top: 7px solid rgba(204, 204, 204, 0.2);
    border-bottom: 0;
}

.wui-select-box.show-menu-arrow.dropup .dropdown-toggle:after {
    bottom: auto;
    top: -3px;
    border-top: 6px solid white;
    border-bottom: 0;
}

.wui-select-box.show-menu-arrow.pull-right .dropdown-toggle:before {
    right: 12px;
    left: auto;
}

.wui-select-box.show-menu-arrow.pull-right .dropdown-toggle:after {
    right: 13px;
    left: auto;
}

.wui-select-box.show-menu-arrow.open>.dropdown-toggle:before,
.wui-select-box.show-menu-arrow.open>.dropdown-toggle:after {
    display: block;
}

