/*** wui-grid Start ****/
.ui-th-ltr,
.ui-jqgrid .ui-jqgrid-htable th.ui-th-ltr {
    text-align: center;
}

#search_grid-table,
#refresh_grid-table {
    visibility: hidden;
}

.ui-jqgrid .ui-jqgrid-bdiv {
    width: 100% !important;
}

.ui-jqgrid .ui-jqgrid-view {
    width: 100% !important;
}

.ui-jqgrid {
    width: 100% !important;
    margin-top: 10px;
}

.ui-jqgrid .ui-jqgrid-pager {
    width: 100% !important;
}

.ui-jqgrid .ui-jqgrid-pager .ui-pg-div {
    width: 0;
    height: 0;
}

.ui-jqgrid .ui-jqgrid-hdiv {
    width: 100% !important;
}

.ui-jqgrid .ui-jqgrid-bdiv{
    min-height: 42px;
}

.ui-jqgrid .ui-jqgrid-hdiv.frozen-div {
    width: auto !important;
}

.ui-jqgrid .ui-jqgrid-bdiv.frozen-bdiv {
    width: auto !important;
}

.ui-jqgrid .ui-jqgrid-htable th.ui-th-ltr, .ui-th-ltr {
    position: relative;
}

.ui-jqgrid .jqg-third-row-header .ui-jqgrid-resize-ltr {
    float: none;
}

.ui-jqgrid .ui-jqgrid-resize-ltr{
    margin: 0px;

    position: absolute;
    top: 8px;
    right: 0px;
    /*z-index: 1000;*/
    /* z-index: 99; */

    width: 1px;
    background: #bbb;
}

.ui-jqgrid .ui-jqgrid-htable thead th div {
    position: static;
    padding-left: 10px;
    padding-right: 10px;
}

/*.ui-jqgrid .ui-jqgrid-resize:after {
    content: "";
    display: inline-block;
    height: 100%;
    width: 1px;
    background: #bbb;
    border-left: 1px solid #bbb;
}

.ui-jqgrid .ui-jqgrid-resize-ltr:hover{
    background: #ddd;
}*/

#grid-pager_center {
    width: 0px;
}

/*wui.grid开始加载图片背景*/
.wui-grid .ui-jqgrid .loading {
    background-color: transparent !important;
}

.grid-opt .dropdown-menu {
    min-width: 60px;
}

.grid-opt .dropdown-menu li a {
    width: 100%;
}

.jqgird-no-text {
    position: relative;
}

.jqgird-no-text td {
    position:absolute;
    top:0;
    left:0;
    right:0;
    text-align: center;
    color:#666;
    border: 1px solid #eee;
    border-top: 0;
    z-index: 100;
}

.jqgird-no-text td div{
    /*line-height: 40px;
    margin: 10px;*/
    line-height: 30px;
    margin: 5px;
    border: 1px dashed #ddd;
}

.wui-grid .w-grid-toolbar .wui-btn {
    margin-right: 5px;
    float: left;
}

.grid-fa-vertical {
	line-height: 30px;
}

/* 表格布局grid搜索条样式-start */
.wui-grid-table>thead>tr>th {
	border: none;
	height: 0px;
	padding: 0px;
}

.wui-grid-table .search-title {
	text-align: right;
	/*vertical-align: middle;*/
    vertical-align: top;
    padding-top: 12px;
}

.wui-grid-table .search-button {
	text-align: right;
	padding-left: 0px;
}

.wui-searchBar-toggle{
	text-align: center;
	color:#666;
	cursor: pointer;
	font-size:16px;
}

/* 表格布局grid搜索条样式-end */


.wui-grid-table.table {
    margin-bottom: 0px;
    border: none;
}

.wui-grid-table.table>tbody>tr>td {
    border-top: none;
}

.wui-grid-table.table td.td-title {
    vertical-align: middle;
    padding-right: 4px;
    white-space: nowrap;
}

.wui-grid-table.table td.td-search-oper {
    vertical-align: middle;
    padding-right: 0px;
    padding-left: 0px;
    width: 20px;
}

.wui-grid .td-content .wui-selectBox {
    position: absolute;
    width: 200px;
}

.wui-grid .wui-grid-searchBar {
    border-bottom: 1px solid #e5e5e5;
}

.wui-grid .wui-grid-searchBar .td-button {
    white-space: nowrap;
}

.wui-grid .wui-grid-searchBar .date-range .date-range-input {
    max-width: 100%;
    white-space: nowrap;

    width: 100%;
    height: 30px;
    padding: 5px 12px;
    border: 1px solid #ccc;
}

.wui-grid .ui-jqgrid .fa.wui-fa-middle {
    top: 3px;
    left: 3px;
}

.w-grid-toolbar {
    padding: 0px;
    margin: 10px 0 0;
}

.ui-jqgrid .ui-jqgrid-htable .ui-th-div .s-ico {
    height: 100%;
    padding-top: 13px;
    margin-top: 0px;
}

.ui-jqgrid .ui-grid-ico-sort {
    height: 5px;
}


.ui-jqgrid .ui-pg-button span{
    border: 1px solid #ccc;
    text-align: center;
    line-height: 30px;
    font-size: 16px;
    font-weight: 500;
}

.ui-jqgrid span.ui-separator {
    height: 18px;
    border: none;
}

.wui-grid-bordered .ui-jqgrid-htable .ui-state-default{
    border: 1px solid #999;
}

.wui-grid-bordered .ui-jqgrid-btable .ui-widget-content > td{
    border: 1px solid #999;
}

.wui-grid-bordered .ui-jqgrid .ui-jqgrid-resize-ltr{
    background: transparent;
    top: 0px;
    /*background: red;*/
}

.ui-search-table{
    width: 100%;
}

/*设置超出文本自动换行*/
.wui-grid-break-line .ui-jqgrid tr.jqgrow td { 
    white-space: normal !important; 
    height:auto; 
}


/**** wui-grid End ***/