/* wui按钮样式 */
.wui-btn {
    display: inline-block;
    padding: 0 14px;
    vertical-align: middle;
    font-size: 14px;
    font-weight: 400;
    line-height: 2;
    text-align: center;
    white-space: nowrap;
    background-image: none;
    border: 1px solid transparent;
    border-radius: 0;
    cursor: pointer;
    outline: 0;
    -webkit-appearance: none;
    text-decoration: none !important;
}

.wui-btn-block {
    display: block;
    width: 100%;
}

.wui-btn-block+.wui-btn-block {
    margin-top: 5px;
}

input[type="submit"].wui-btn-block,
input[type="reset"].wui-btn-block,
input[type="button"].wui-btn-block {
    width: 100%;
}

.wui-btn-xl {
    font-size: 18px;
}

.wui-btn-lg {
    font-size: 16px;
}

.wui-btn-sm {
    font-size: 12px;
}

.wui-radius {
    border-radius: 4px;
}

.wui-round {
    border-radius: 1000px;
}

.wui-text-indent {
    text-indent: 10px;
}

.icon-btn {
    height: 60px;
    min-width: 80px;
    margin: 5px 5px 0 0;
    border: 1px solid #ddd;
    padding: 12px 0 0;
    background-color: #fafafa;
    background-image: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    display: inline-block;
    color: #646464;
    text-shadow: none;
    text-align: center;
    cursor: pointer;
    position: relative;
}

.icon-btn>div {
    margin-top: 5px;
    margin-bottom: 20px;
    color: #666;
    font-size: 12px;
}

.icon-btn:hover {
    color: #333;
}

.icon-btn>.badge {
    position: absolute;
    font-weight: 300;
    top: -5px;
    right: -5px;
    font-size: 12px;
}

/* 折叠面板 */
.wui-accordion .panel {
    box-shadow: none;
    border: none;
    border-radius: 0;
    margin-bottom: 6px;
}

.wui-accordion .panel-heading {
    padding: 0;
    position: relative;
}

.wui-accordion .panel-title a {
    display: block;
    color: #333;
    font-size: 14px;
    padding: 14px 40px;
    background: #f5f5f5;
    /*text-transform: uppercase;*/
    border-bottom: 1px solid #d3d3d3;
    position: relative;
}

.wui-accordion .panel-title a.collapsed {
    border-top: 0;
    color: #333;
    background: #eee;
    border-bottom: 1px solid transparent;
}

.wui-accordion .panel-heading {
    /* border-left:3px solid #4B83FA; */
    border-radius: 0;
}

.wui-accordion .panel-heading+.panel-collapse>.panel-body {
    border-top: 0;
}

.wui-accordion .panel-title a {
    display: block;
    color: #333;
    font-size: 14px;
    padding: 12px 40px;
    background: #f3f5f7;
    border-bottom: 1px solid #e5e5e5;
    position: relative;
    text-decoration: none;
    box-shadow: none;
}

.wui-accordion .panel-title a.collapsed {
    border-top: 0;
    color: #333;
    background: #eee;
    border-bottom: 1px solid transparent;
}

.wui-accordion .panel-title a:before,
.wui-accordion .panel-title a.collapsed:before {
    content: "\f068";
    font-family: FontAwesome;
    position: absolute;
    top: 10px;
    left: 15px;
    font-size: 14px;
    line-height: 24px;
    /* color: #4B83FA; */
}

.wui-accordion .panel-title a.collapsed:before {
    content: "\f067";
}

.wui-accordion .panel-heading .btn-group {
    position: absolute;
    right: 15px;
    top: 5px;
}

/** wui-date End  */
.has-feedback.wui-has-feedback {
    margin-top: 8px;
