}

.wui-has-feedback .wui-input {
    padding-right: 42.5px;
}

.wui-has-feedback .form-control-feedback {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    display: block;
    width: 38px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    pointer-events: none;
}

