
.body-full-height{
	height: 100%;
}

.body-full-height .portlet{
	height: 100%;
}

.portlet {
	width: 100%;
	background: #EEEFF2;
}

.portlet .portlet-body {
	background: #fff;
}

.portlet .portlet-title {
	position: relative;
	font-size: 16px;
	color: #333;
	background: #EEEFF2;
	padding: 10px 20px;
}

.portlet .portlet-title.portlet-title-fixed {
	position: fixed !important;
	top: 0;
	left: 0;
	right: 0;
	z-index: 10000;
}

.portlet-title .btn-group,
.portlet-title.portlet-title-fixed .btn-group {
	position: absolute;
	bottom: 0px;
	right: 0px;
}

.portlet .portlet-title .btn-group .btn {
	margin-left: 10px;
}

.portlet .portlet-body {
	padding-top: 20px;
	padding-left: 20px;
	padding-right: 20px;
	/*margin: 0 20px;*/
	margin: 0 10px;
}

.portlet-body-title {
	font-size: 16px;
	color: #333;
	line-height: 26px;
	margin-bottom: 20px;
	margin-top: 0;
}

.portlet .portlet-footer {
	margin-top: 20px;
}

.portlet .portlet-footer .btn {
	float: left;
	margin-right: 10px;
}

.portlet-l {
	width: 20%;
	float: left;
	border-right: 1px solid #e5e5e5;
}

.portlet-r {
	width: 80%;
	float: right;
}

.portlet-l .portlet-body-title,
.portlet-r .portlet-body-title {
	font-size: 16px;
	color: #333;
	line-height: 40px;
	margin: 0 0 10px 0;
	padding: 0 20px;
	background: #f3f5f7;
}

.iframe-body {
	height: 100%;
}

.iframe-body .portlet {
	background: #fff;
	height: 100%;
}

.iframe-body .portlet .portlet-title {
	display: none;
}

.iframe-body .portlet .portlet-body {
	margin: 0 0;
	padding: 0;
}

/* 基础组件样式 */
.typeset {
	margin-bottom: 30px;
}

.typeset .typeset-title {
	font-size: 16px;
	color: #333;
	margin-bottom: 20px;
}

.typeset .typeset-title i {
	margin-right: 10px;
}

.typeset .typeset-body-title {
	font-size: 14px;
	color: #333;
}

.typeset-text {
	margin: 10px 0;
}

.typeset-table {
	width: 100%;
}

.typeset-table tr th,
.typeset-table tr td {
	height: 40px;
}

.typeset-table button {
	width: 80px;
	height: 30px;
}
