
/**
* 选项卡样式修复
*/
.tab-full-height {
    height: 340px;
}

/* 关闭按钮 */
.nav > li > a.is-close {
    padding: 10px 25px 10px 15px;
}

ul.nav li a>i.tab-close {
    margin-right: -14px;
    margin-left: 3px;
    cursor: pointer;
}

/* tab默认风格样式 */
.tab-default .nav-tabs>li>a {
	color: #666;
	font-size: 14px;
}

/* tab简洁风格样式 */
.tab-concise .nav-tabs {
	border-bottom: 1px solid #e5e5e5;
}

/*.tab-concise .nav-tabs>li {
	margin-right: 56px;
}*/

.tab-concise .nav-tabs > li {
	/*padding-right: 56px;*/
	padding-left: 28px;
	padding-right: 28px;
}

.tab-concise .nav-tabs>li>a {
	color: #666;
	font-size: 14px;
	border: 0;
}

.tab-concise .nav-tabs>li>a:hover {
	background: transparent;
	border: 0;
}

.tab-concise .nav-tabs>li.active>a,
.tab-concise .nav-tabs>li.active>a:focus,
.tab-concise .nav-tabs>li.active>a:hover {
	background: transparent;
	/* border-bottom: 3px solid #4b83FA; */
	border-top: 0;
	border-left: 0;
	border-right: 0;
	margin-bottom: -3px;
	color: #333;
}

.tabs-horizontal {
	position: relative;
	height: 100%;
}

.wui-tabs.tabs-horizontal .nav {
	position: absolute;
	top: 0;
	width: 150px;
}

.wui-tabs.tabs-horizontal .nav.nav-left {
	left: 0;
}

.wui-tabs.tabs-horizontal .nav.nav-right {
	right: 0;
}

.wui-tabs.tabs-horizontal .nav.nav-left>li.active>a,
.wui-tabs.tabs-horizontal .nav.nav-left>li.active>a:focus,
.wui-tabs.tabs-horizontal .nav.nav-left>li.active>a:hover {
	background: transparent;
	border-color: transparent;
	color: #333;
}

.wui-tabs.tabs-horizontal .nav.nav-right>li.active>a,
.wui-tabs.tabs-horizontal .nav.nav-right>li.active>a:focus,
.wui-tabs.tabs-horizontal .nav.nav-right>li.active>a:hover {
	background: transparent;
	border-color: transparent;
	border-left: 3px solid #4b83FA;
	color: #333;
}

.tabs-horizontal .tab-content {
	display: block;
	/*margin-left:180px;*/
}
