/* WUI Container Start */
.wui-container-light {
    background-color: #ecf0f5;
}

.wui-container-light .light,
.wui-container-light .light .wui-container-main-title,
.wui-container-light .light.tab-concise .nav.nav-tabs {
    background-color: #fff;
}

/*.wui-container,
.wui-container-header,
.wui-container-main,
.wui-container-main-body,
.wui-container-main-top,
.wui-container-main-bottom,
.wui-container-main-left,
.wui-container-main-right,
.wui-container-main-title,
.wui-container-main-content,
.wui-container-footer{
    position: relative;
}*/

.wui-container{
    position: relative;
}

.wui-container-header{
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    min-height: 60px;
    line-height: 60px;
    padding: 0px;
}


.wui-container-main {
    padding: 60px 0px;
}

.wui-container-main-body,
.wui-container-main-left,
.wui-container-main-right{
    position: relative;
    min-height: 1px;
    padding-right: 0px;
    padding-left: 0px;
}

.wui-container-main-body{
    width: 100%;
}

.wui-container-main-top {
    position: relative;
    width: 100%;
    height: 30%;
}

.wui-container-main-bottom {
    position: absolute;
    width: auto; 
    bottom: 0px;
    top: 30%;
    left: 0px;
    right: 0px;
}


.wui-container .wui-container-main-left  .wui-container-main-top,
.wui-container .wui-container-main-right  .wui-container-main-top{
    padding-left: 0px;
    padding-right: 0px;
}


.wui-container-main .wui-container-main-title {
    font-size: 14px;
    margin: 0;
    padding: 16px;
    background: #f3f5f7;
    border-bottom: 1px solid #e5e5e5;
}

.wui-container-main .wui-container-main-content{
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    padding: 10px 16px;
}

.wui-container-main .wui-container-main-title + .wui-container-main-content{
    top: 48.2px;
}

.wui-container-footer{
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    min-height: 60px;
    line-height: 60px;
}

.wui-container-footer .wui-container-footer-actions {
    line-height: 60px;
}

/*隐藏头尾*/
.wui-container-header-hide .wui-container-main{
    padding-top: 0px;
}

.wui-container-header-hide .wui-container-header{
    display: none;
}

.wui-container-footer-hide .wui-container-main{
    padding-bottom: 0px;
}

.wui-container-footer-hide .wui-container-footer{
    display: none;
}

/*固定头尾*/
.wui-container-header-fixed .wui-container-header{
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    z-index: 9999;
}
.wui-container-footer-fixed .wui-container-footer{
    position: fixed;
    z-index: 9999;
}

/*添加内边距 10px*/
.wui-container-padding-10 .wui-container-header{
    padding: 10px;
    line-height: 40px;
}
.wui-container-padding-10.wui-container-header-hide .wui-container-main {
    padding-top: 10px;
}
.wui-container-padding-10.wui-container-footer-hide .wui-container-main {
    padding-bottom: 10px;
}
.wui-container-padding-10 .wui-container-main{
    padding-right: 5px;
    padding-left: 5px;
}

.wui-container-padding-10 .wui-container-main-body,
.wui-container-padding-10 .wui-container-main-left,
.wui-container-padding-10 .wui-container-main-right,
.wui-container-padding-10 .wui-container-main-top{
    padding-right: 5px;
    padding-left: 5px;
}

.wui-container-padding-10 .wui-container-main-top{
     padding-bottom: 10px;
}
.wui-container-padding-10 .wui-container-main-top .wui-container-main-content{
     bottom: 10px;
}

.wui-container-padding-10 .wui-container-main-bottom{
    left: 5px;
    right: 5px;
}

.wui-container-padding-10 .wui-container-footer{
    padding: 10px;
    line-height: 40px;
}

.wui-container-padding-10 .wui-container-footer .wui-container-footer-actions {
    line-height: 40px;
}

/*添加内边距 15px*/
