/*WUI Form Start*/
.wui-form {
    width: 100%;
}

.wui-form-item{
    float: left;
    width: 50%;
}

.wui-form .wui-form-label {
    width: 120px;
    text-align: right;
    /*padding: 5px 10px 0 0;*/
    vertical-align: middle;
    font-size: 14px;

    /*color: #333;*/

    float: left;
    color: #606266;
    line-height: 40px;
    padding: 0 12px 0 0;
    box-sizing: border-box;
}

