
/*
  uiChooseBox公共页面样式修复
*/
html,
.selector-page {
    height: 100%;
    margin: 0;
    padding: 0;
    background-color: #fff;
    /*padding: 0 0 50px 0;*/
}

.page-container {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height: 100%;
}

.ui-selector-tree {
    width: 320px;
    border-right: 5px solid #DCDCDC
}

.ui-selector-chosen {
    width: 320px;
    /*border-right: 5px solid #DCDCDC*/
    position: relative;
}

.ui-selector-chosen .page-foot {
    bottom: 5px;
    text-align: center;
}

.ui-selector-list {
    width: 100%;
    /*width: 684px;*/
    border-right: 5px solid #DCDCDC
}

.ui-selector-tree,
.ui-selector-list,
.ui-selector-chosen {
    height: 100%;
}

.tree-head h4,
.grid-caption h4,
.chosen-append h4 {
    padding: 0 8px;
    margin: 0;
    border-bottom: 1px solid #e5e5e5;
    /*line-height: 42px;*/
    color: #333;
    font-size: 16px;
}

.grid-caption h4,
.chosen-append h4 {
    line-height: 42px;
}

.tree-head h4 {
    padding-top: 12px;
    padding-bottom: 13px;
}

.tree-head .wui-form-check {
    font-size: 14px;
    float: right;
    margin-right: 2px;
}

.tree-head .wui-form-check label {
    width: 16px;
    height: 16px;
    line-height: 16px;
    font-size: 12px;
    margin-right: 0px;
}

.iframe-list .tab-overflow {
    width: 100%;
}

.tab-overflow.iframe-tree {
    left: 280px;
}

.popup-page {
	position: relative;
	height: 100%;
	margin: 0;
	padding: 10px 20px;
	background-color: #ecf0f5;
}

.popup-page .popup-page-body {
	display: block;
	margin-bottom: 50px;
}

.popup-page .popup-page-body .popup-page-items {
	height: 100px;
	background-color: #fff;
	padding: 15px;
	margin-bottom: 10px;
}

.popup-page .popup-page-body .popup-page-items .tag-container {
	border: none;
}

.popup-page .popup-page-body .popup-page-tab {
	background-color: #fff;
	padding: 15px 15px 0 15px;
	height: 100%;
	width: 100%;
}

.popup-page .popup-page-body .popup-page-tab .popup-tab-container {
	height: 100%;
	width: 100%;
}

.popup-page .popup-page-body .popup-page-tab .main-module {
	position: relative;
	width: 100%;
	height: 100%;
}

.popup-page .popup-page-body .popup-page-tab .main-module .module-tree {
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	width: 320px;
	height: 100%;
	/*overflow-y: scroll;*/
	border-right: 5px solid rgb(220, 220, 220);
}

.popup-page .popup-page-body .popup-page-tab .main-module .module-list {
	display: block;
	margin-left: 320px;
	height: 100%;
	/*overflow-y: scroll;*/
}

.popup-page .popup-page-footer {
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: auto;
	text-align: right;
	z-index: 50;
}

.popup-page .popup-page-footer .footer-actions {
	padding: 10px 20px;
}

.popup-page.page-horizontal {
	padding: 0;
}

.popup-page.page-horizontal .popup-page-body {
	height: 100%;
	width: 100%;
	margin: 0;
	padding: 0px;
}

.popup-page.page-horizontal .popup-page-body {
	margin-bottom: 0px;
	height: 100%;
}

.popup-page.page-horizontal .popup-page-body .popup-page-tree {
	position: absolute;
	left: 0;
	top: 0;
	width: 220px;
	height: 100%;
	z-index: 50;
	border-right: 5px solid #DCDCDC;
	background-color: #fff;
}

.popup-page.page-horizontal .popup-page-body .popup-page-tab {
	display: block;
	height: 100%;
	width: auto;
	margin-left: 220px;
	margin-right: 220px;
	border-right: 5px solid #DCDCDC;
	padding: 0;
}

.popup-page.page-horizontal .popup-page-body .popup-page-tab .tab-concise {
	height: 100%;
}

.popup-page.page-horizontal .popup-page-body .popup-page-items {
	position: absolute;
	right: 0;
	top: 0;
	width: 220px;
	height: 100%;
	z-index: 50;
	margin-bottom: 0;
	padding: 0;
}

.popup-page.page-horizontal.page-hide-tree .popup-page-body .popup-page-tree {
	display: none;
}

.popup-page.page-horizontal.page-hide-tree .popup-page-body .popup-page-tab {
	margin-left: 0px;
	/*padding-left: 10px;*/
}

.popup-page.page-horizontal.page-hide-list .popup-page-body .popup-page-tree {
	position: static;
	display: block;
	height: 100%;
	width: auto;
	margin-right: 220px;
	padding: 0;
}

.popup-page.page-horizontal.page-hide-list .popup-page-body .popup-page-tab {
	display: none;
}

.popup-page.page-horizontal.page-hide-chosen .popup-page-body .popup-page-items{
	display: none;
}

.popup-page.page-horizontal.page-hide-chosen .popup-page-body .popup-page-tab,
.popup-page.page-horizontal.page-hide-list.page-hide-chosen .popup-page-body .popup-page-tree {
	margin-right: 0px;
}
